import React from "react";
import { Helmet } from "react-helmet";
import favicon from '../../static/favicon.png'


export default function Layout({ children }) {
  return (
    <>
      <Helmet>
        <link rel="icon" href={favicon} type="image/x-icon" />
        <title>My Website</title>
      </Helmet>
      {children}
    </>
  );
}
